<template>
  <div>
    <div class="container p-0">
      <div>
        <swiper :options="swiperOption" class="">
          <swiper-slide v-for="item in filterData" :key="item.id">
            <ItemCard :card-data="item" :loadingStatus="status" @addtoCart="addtoCart"></ItemCard>
          </swiper-slide>
          <div class="swiper-pagination" slot="pagination"></div>
        </swiper>
      </div>
    </div>
  </div>
</template>

<script>
import ItemCard from '@/components/ItemCard'
import { mapActions, mapGetters } from 'vuex'

export default {
  name: 'home',
  components: {
    ItemCard
  },
  data () {
    return {
      swiperOption: {
        spaceBetween: 10, // slidepx
        centeredSlides: false, // trueactive slide
        grabCursor: true, // trueSwiper
        pagination: {
          el: '.swiper-pagination',
          clickable: true // trueSwiper
        },
        autoplay: {
          delay: 4000
        },
        slidesPerView: 4, // sliderslides(carousel)
        slidesPerGroup: 4, // carousel modeslides
        breakpoints: {
          480: {
            slidesPerView: 1,
            slidesPerGroup: 1,
            spaceBetween: 20
          },
          767: {
            slidesPerView: 2,
            slidesPerGroup: 2,
            spaceBetween: 10
          },
          992: {
            slidesPerView: 3,
            slidesPerGroup: 3,
            spaceBetween: 20
          }
        }
      }
    }
  },
  props: {
    card: {
      type: Object,
      default: function () {
        return {}
      }
    }
  },
  methods: {
    addtoCart (id, qty = 1) {
      this.$store.dispatch('cartModules/addtoCart', { id, qty })
    },
    ...mapActions('productsModules', ['getProducts'])
  },
  computed: {
    filterData () {
      const vm = this
      // console.log(vm.card.category)
      if (!vm.card.category) {
        return vm.products
      } else {
        return vm.products.filter((item) => {
          return item.category === vm.card.category
        })
      }
    },
    ...mapGetters(['isLoading', 'status']),
    ...mapGetters('productsModules', ['categories', 'products'])
  },
  created () {
    this.getProducts()
  }
}
</script>

<style lang="scss" scoped>
.swiper-container{
  min-height: 380px;
}
</style>
